import dayjs from 'dayjs'

// 
let doLastTimeout, doLastOperates = []
// 
const timeout = 500

export default {
  dayjs,
  /**
   * 
   * @param {function} operate 
   * @param {number} idx ()
   */
  doAsyncLast(operate, time = timeout, idx) {
    if (typeof operate !== 'function') {
      throw 'doLast'
    }
    clearTimeout(doLastTimeout)
    doLastTimeout = setTimeout(() => {
      const lastOperate = doLastOperates[doLastOperates.length - 1]
      lastOperate()
      doLastOperates = []
      clearTimeout(doLastTimeout)
      doLastTimeout = null
    }, time)
    doLastOperates.push(operate)
  },
  /**
   * 
   * @param {function} operate 
   * @param {number} idx ()
   */
  doSyncLast(operate, time = timeout, idx) {
    if (typeof operate !== 'function') {
      throw 'doLast'
    }
    if (!doLastTimeout) {
      doLastTimeout = setTimeout(() => {
        const lastOperate = doLastOperates[doLastOperates.length - 1]
        lastOperate()
        doLastOperates = []
        clearTimeout(doLastTimeout)
        doLastTimeout = null
      }, time)
    }
    doLastOperates.push(operate)
  },
  /**
   * 0
   * @params {number} num 
   * @params {number} length 2
   */
  pointLeftNumberLength(num, length = 2) {
    if(typeof(num) === 'number') {
      let numStr = String(num)
      const leftLength = numStr.split('.')[0].length
      if(length > leftLength) {
        const lengthCut = length - leftLength
        let zeroStr = Array.from({length: lengthCut}, () => '0').join('')
        numStr = zeroStr + numStr
      }
      return numStr
    } else {
      throw '!'
    }
  },
  /**
   * dayjs
   * @param {any} time 
   * @param {string} format 
   */
  formatDate(time, format) {
    return dayjs(time).format(format || 'YYYY-MM-DD')
  },
  /**
   * 
   * @param {any} time 
   */
  formatDateDayByDay(time) {
    const cur = dayjs()
    const diff = dayjs(time).diff(cur, 'day')
    // console.log(diff)
    if (Math.abs(diff) > 2) {
      return dayjs(time).format('YYYY-MM-DD')
    } else {
      let res
      switch (diff) {
        case 2:
          res = ''
          break
        case 1:
          res = ''
          break
        case 0:
          res = ''
          break
        case -1:
          res = ''
          break
        case -2:
          res = ''
          break
      }
      return res
    }
  },
  /**
   * 
   * @param start {number} 
   * @param length {number} 
   */
  replaceStar(str, start, length) {
    const regexp = new RegExp(`^(.{${start}}).{${length}}(.*)$`, 'g')
    return str.replace(regexp, `$1${Array.from({length}, () => '*').join('')}$2`)
  },
  /**
   * 
   */
  getCurrentPageUrl() {
    const pages = getCurrentPages()
    const currentPage = pages[pages.length - 1]
    const url = `/${currentPage.route}`
    return url
  },
  /**
   * 
   */
  getCurrentPageUrlWithArgs() {
    const pages = getCurrentPages()
    const currentPage = pages[pages.length - 1]
    const url = currentPage.route
    const options = currentPage.options
    let urlWithArgs = `/${url}?`
    for (let key in options) {
      const value = options[key]
      urlWithArgs += `${key}=${value}&`
    }
    urlWithArgs = urlWithArgs.substring(0, urlWithArgs.length - 1)
    return urlWithArgs
  },
  /**
   * 
   * @param {string} path 
   */
  getPathParams(path) {
    const search = path.split('?')[1]
    if (search) {
      const key_value_list = search.split('&')
      const obj = {}
      key_value_list.forEach(key_value => {
        let temp_arr = key_value.split('=')
        obj[temp_arr[0]] = temp_arr[1]
      })
      return obj
    } else {
      return {}
    }
  },
  /**
   * 
   * @param {number} level
   */
  getLingyangLevelLabel(level) {
    let label
    switch(level) {
      case 0: label = ''
      break
      case 1: label = ''
      break
      case 2: label = ''
      break
      case 3: label = ''
      break
      case 4: label = ''
      break
      default: label = ''
    }
    return label
  },
  /**
   * 
   */
  getCategoryLabel(number) {
    const categorys = ['', '', '', '', '', '', '', '', '']
    return categorys[number] || ''
  },
}